import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Usuario } from '../../../modelo/Usuario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'usuario-dados-details',   templateUrl: 'usuario_dadosDetails.component.html' }) export class UsuarioDadosDetailsComponent implements OnInit {    usuario: Usuario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.usuario.Usuario";                 let id = +params['id'];         this.usuario = new Usuario();         this.usuario.cd_usuario = id;         o.objetoJson = this.usuario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a usuario. '; }      this.usuario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


