import { Component, OnInit } from '@angular/core'; import { Router } from '@angular/router';  @Component({   moduleId: module.id,   selector: 'usuario-dados',   templateUrl: 'usuario_dados.component.html' }) export class UsuarioDadosComponent implements OnInit {       displayMode: UsuarioDadosDisplayModeEnum;     displayModeEnum = UsuarioDadosDisplayModeEnum;       constructor(private router: Router) { }      ngOnInit() {       const path = this.router.url.split('/')[3];       switch (path) {         case 'details':           this.displayMode = UsuarioDadosDisplayModeEnum.Details;           break;         case 'info':           this.displayMode = UsuarioDadosDisplayModeEnum.Orders;           break;         case 'edit':           this.displayMode = UsuarioDadosDisplayModeEnum.Edit;           break;       }     }  }  enum UsuarioDadosDisplayModeEnum {   Details=0,   Orders=1,   Edit=2 }


